  transition: all 0.6s cubic-bezier(0.68, -0.55, 0.27, 1.55); }

.portfolio .image-content .background:nth-of-type(1):before {
  transition: all 0.6s cubic-bezier(0.68, -0.55, 0.27, 1.55); }

.portfolio .image-content:hover .background:nth-of-type(1):before {
  background-color: var(--main-bg-color);
  background-blend-mode: multiply;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  content: '';
  opacity: 0.5;
  transition: all 0.6s cubic-bezier(0.68, -0.55, 0.27, 1.55); }

.line-before {
  position: relative; }
  .line-before:before {
    content: '';
    position: absolute;
    top: 11px;
    background: var(--main-color);
    width: 50px;
    height: 1px;
    right: unset;
    left: -55px; }

.portfolio .section-text .line-before:before {
  content: '';
  position: absolute;
  top: 11px;
  background: var(--main-color);
  width: 50px;
  height: 1px;
  right: unset;
  left: -55px; }

.portfolio .link {
  text-transform: capitalize;
  font-weight: 600 !important;
  display: inline-flex !important; }
  .portfolio .link.animate-line-through:hover:before {
    -webkit-transform: scale(0) !important;
    transform: scale(0) !important; }
  .portfolio .link.animate-line-through.active:before {
    -webkit-transform: scale(1) !important;
    transform: scale(1) !important; }
  .portfolio .link.animate-line-through:before {
    top: 7px !important; }

