.rtl-body {
  /* ////////////////// */
  /* home page */
  /* ///////////////// */
  /* ///////////////// */
  /* services page  */
  /* //////////////// */
  /* ///////////////// */
  /* about page */
  /* //////////////// */ }
  .rtl-body body,
  .rtl-body p {
    font-family: 'Cairo', sans-serif; }
  .rtl-body h1,
  .rtl-body h2,
  .rtl-body h3,
  .rtl-body h4,
  .rtl-body h5,
  .rtl-body h6,
  .rtl-body a,
  .rtl-body li {
    font-family: 'Cairo', sans-serif !important;
    /* letter-spacing: 0 !important; */ }
  .rtl-body .button {
    font-family: 'Cairo', sans-serif !important; }
  .rtl-body .right {
    text-align: left !important; }
  .rtl-body .grid [class*=col-] {
    float: right; }
  .rtl-body .panel .sections .menu li a {
    letter-spacing: 0 !important; }
  .rtl-body .left {
    text-align: right !important; }
  .rtl-body .margin-left-5 {
    margin-right: 50px !important;
    margin-left: 0 !important; }
  .rtl-body .button.actionButton .material-icons {
    left: 0 !important; }
  .rtl-body .side {
    right: 100px;
    left: unset; }
  @media screen and (min-width: 1023px) {
    .rtl-body .project .images {
      right: unset;
      left: 0px; } }
  .rtl-body .header.panel {
    left: unset;
    border-left: 1px solid #dde0e4;
    border-right: 0; }
  .rtl-body .project .images p {
    left: unset;
    right: -155px; }
    .rtl-body .project .images p:after {
      top: 50%;
      left: -75%; }
  .rtl-body .project .section-text p.small {
    margin-right: 10rem;
    margin-left: unset; }
    .rtl-body .project .section-text p.small:before {
      right: -75%;
      left: unset; }
  .rtl-body .sidebar.visible {
    right: 0px;
    left: unset;
    direction: rtl; }
  @media screen and (min-width: 1023px) {
    .rtl-body .sidebar.visible {
      -webkit-transform: translateX(-100px) !important;
      transform: translateX(-100px) !important; } }
  @media screen and (min-width: 1023px) {
    .rtl-body .header.panel .sections .center {
      left: unset;
      right: 30px; }
    .rtl-body .header.panel .sections .right {
      right: 40px;
      left: unset; } }
  .rtl-body .navigation ul {
    -webkit-transform: rotate(90deg);
    transform: rotate(90deg);
    -webkit-transform-origin: center right;
    transform-origin: center right; }
  @media screen and (min-width: 1023px) {
    .rtl-body .wrapper {
      margin-right: 15.8rem;
      margin-left: unset !important; } }
  .rtl-body .line-before:before {
    left: unset !important;
    right: -55px !important; }
  .rtl-body .services .align-letter {
    right: 47%;
    left: unset;
    transform: translateX(43%) translateY(43%);
    -webkit-transform: translateX(43%);
    top: 48%;
    /* letter-spacing: 0; */ }
  .rtl-body .services .letter,
  .rtl-body .services .letter-mask {
    right: 0;
    left: unset; }
  .rtl-body .letter .align-letter[data-letter="present"] {
    right: 40%; }
  .rtl-body .about .box {
    right: 105px;
    left: unset !important; }
  .rtl-body .about .image-content:nth-of-type(2n) {
    right: 50px;
    left: unset; }
  .rtl-body .about .image-content:nth-of-type(2n+1) {
    left: unset;
    right: 155px; }
  .rtl-body .line-before.margin-left-6 {
    margin-left: unset !important;
    margin-right: 6rem; }
